<template>
  <div class="root" :style="{ height: v_heith }">
    <video
      autoplay
      playsinline
      muted
      controls
      id="player-video"
      type="module"
      src="../utils/webrtc/page_play.js"
    ></video>
  </div>
</template>

<script>
export default {
  data() {
    return {
      v_heith: "0px"
    };
  },
  created() {
    const calculate_height = (window.innerWidth * 9) / 16;
    this.v_heith = calculate_height + "px";

    this.$nextTick(() => {
      const video = document.getElementById("player-video");
      console.log(video);
      window.wowza_video = video;
    });
  }
};
</script>

<style scoped>
.root {
  position: relative;
  /* background: black; */
}

#player-video {
  background: black;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
</style>